<template>
  <div class="container py-4 mt-2 projects">
    <!-- <h1 @click="Click()">click</h1> -->
    <h2>Projects I've done</h2>
    <div class="row py-1 pb-3">
      <!-- single card  -->
      <div
        class="col-md-4 col-sm-12"
        v-for="project in projects"
        :key="project.id"
      >
        <div class="ProjectBox p-1 my-3">
          <div class="card mx-auto bg-white text-dark my-3">
            <div class="cardTopButton" v-if=project.visitLink>
              <a
              :href=project.visitLink
              target="_blank"
                type="button"
                class="btn btn-secondary text-small gradientBackground py-1 m-1"
              >
                Visit Link
              </a>
            </div>
            <div
              class="card-img-top projectImage"
              data-fancybox
              :data-src="'#hidden-content' + project.id"
              :style="{ backgroundImage: `url(${project.images[0]})` }"
            ></div>
            <!-- style="background-image: url('img/profilePic3.jpg')" -->
            <div class="card-body p-1">
              <h6 class="card-title mb-1">{{ project.title }}</h6>
              <p class="card-text d-lg-flex wrap mb-1 justify-content-center">
                <button
                  type="button"
                  class="btn btn-secondary btn-sm ProjectBox my-1 py-0"
                  v-for="(helpers, index) in project.helpers"
                  :key="index"
                >
                  {{ helpers }}
                </button>
              </p>
              <div class="text-center text-small bold">
                {{ project.description }}
                <div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- fancy Box Modal -->
        <div :id="'hidden-content' + project.id" class="fancyBoxModal">
          <div class="text-center">Click One</div>

          <template v-for="(image, index) in project.images" :key="index">
            <a :href="`${image}`" :data-fancybox="'gallery' + project.id">
              <img class="m-2 smallBoxImage" :src="image" alt="image" />
            </a>
          </template>
        </div>
      </div>
      <!-- //////single card end  -->
    </div>
    <h2>Personal Projects</h2>
    <div class="row py-1 pb-3">
      <!-- single card  -->
      <div
        class="col-md-4 col-sm-12"
        v-for="universityProject in universityProjects"
        :key="universityProject.id"
      >
        <div class="ProjectBox p-1 my-3">
          <div class="card mx-auto bg-white text-dark my-3">
             <div class="cardTopButton" v-if=universityProject.visitLink>
              <a
              :href=universityProject.visitLink
              target="_blank"
                type="button"
                class="btn btn-secondary text-small gradientBackground py-1 m-1"
              >
                Visit Link
              </a>
            </div>
            <div
              class="card-img-top projectImage"
              data-fancybox
              :data-src="'#hidden_Uni_content' + universityProject.id"
              :style="{
                backgroundImage: `url(${universityProject.images[0]})`,
              }"
            ></div>
            <!-- style="background-image: url('img/profilePic3.jpg')" -->
            <div class="card-body p-1">
              <h6 class="card-title mb-1">{{ universityProject.title }}</h6>
              <p class="card-text d-lg-flex wrap mb-1 justify-content-center">
                <button
                  type="button"
                  class="btn btn-secondary btn-sm ProjectBox py-0 my-1"
                  v-for="(helpers, index) in universityProject.helpers"
                  :key="index"
                >
                  {{ helpers }}
                </button>
              </p>
              <div class="text-center text-small">
                {{ universityProject.description }}
              </div>
            </div>
          </div>
        </div>
        <!-- fancy Box Modal -->
        <div
          :id="'hidden_Uni_content' + universityProject.id"
          class="fancyBoxModal"
        >
          <div class="text-center">Click One</div>

