<template>
  <div class="wrapper mt-4 experience CollapseHome">
    <div class="main-title">
      &nbsp;<i class="fas fa-school fa-2x mx-3"></i>Education
    </div>
    <!-- <input type="radio" id="radioYour Account" name="accordion" checked="checked" /> -->
    <!-- {{firstExperienceKey=experience[0].id}} -->
    <template v-for="(institutesList, index) in institutes" :key="index">
      <input
        type="radio"
        :id="'experienceCollapse' + index"
        name="'accordion' + index"
        class="collapseRadio"
      />
      <label class="item" :for="'experienceCollapse' + index">
        <div class="title">
          <span
            >
            <i class="fas  mx-2" :class="institutesList.icon"></i>
            {{ institutesList.name }} <span v-if="institutesList.startingDate">
               (<small
              >{{ institutesList.startingDate }} -
              {{ institutesList.endDate }}</small
            >)
            </span>
          </span>
        </div>

        <div class="content">
          <div class="font-weight-bold">
            <small> {{ institutesList.grade }}</small>
          </div>
          <div
            class="font-weight-light"
            style="white-space: pre-line; text-align: left"
          >
            {{ institutesList.description }}
          </div>
        </div>
      </label>
    </template>
    <input
      type="radio"
      id="radioPayment &amp; Pricing"
      name="accordion"
      class="collapseRadio"
    />
    <label class="item" for="radioPayment &amp; Pricing">
      <div class="title">
        <span> <i class="fas fa-school"></i> End</span>
      </div>
      <div class="content"></div>
    </label>
  </div>
</template>

<script>
export default {
  name: "Education",
  // data(){
  //     return {
  //         firstExperienceKey:experienceList[0].id
  //     }
  // },
  computed: {
    institutes() {
      return this.$store.getters["jsonDataStore/displayInstitutes"];
    },
   
  },
  mounted: function () {
    $(".experience .collapseRadio:first").attr("checked", "checked");
  },

};
</script>

